import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: Home
  }, {
    path: '/city',
    name: 'City',
    component: City
  }, {
    path: '/detail/:id', // :id 动态router
    name: 'Detail',
    component: Detail
  }],
  scrollBehavior (to, from, savedPosition) { // 切换路由时页面的显示位置
    return { x: 0, y: 0 }
  }
})



